    showIcon
    :content-line=&quot;2&quot;
  /&gt;
  &lt;mku-alert
    type=&quot;success&quot;
    title=&quot;这是一条success提示&quot;
    content=&quot;这是一小段副标题&quot;
    showIcon
  /&gt;
  &lt;mku-alert
    type=&quot;error&quot;
    title=&quot;这是一条error提示&quot;
    content=&quot;这是一小段副标题&quot;
    showIcon
  /&gt;
