    type=&quot;warning&quot;
    title=&quot;这是一条warning提示&quot;
    content=&quot;这是一小段副标题&quot;
    showIcon
  /&gt;
&lt;/div&gt;
</code></pre></template></mku-example><h3>可关闭</h3>
