    showIcon
    closeable
  /&gt;
&lt;/div&gt;
</code></pre></template></mku-example><h3>Alert Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">alert警告类型</td>
<td style="text-align:left">String</td>
<td style="text-align:left">info、error、success、warning</td>
<td style="text-align:left">info</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">title</td>
<td style="text-align:left">alert的标题</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">是</td>
</tr>
<tr>
<td style="text-align:left">showIcon</td>
<td style="text-align:left">是否显示图标</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">content</td>
<td style="text-align:left">alert的描述，为空或不填时不显示描述</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">content-line</td>
<td style="text-align:left">描述最多显示几行，溢出隐藏</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">closeable</td>
<td style="text-align:left">是否可以关闭，开启会显示关闭按钮</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Icon Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">close</td>
<td style="text-align:left">点击关闭时触发</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>

